#app-container {
  position: relative;
  width: 100%;
  height: 100vh;
}

ul.menu {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;

    li {
        & > a {
            display: block;
            color: white;
            text-align: center;
            text-decoration: none;
        }
    }

    &.h {
        li {
            float: left;

            & > a {
                padding: 15px 25px;
            }

            &:hover {
                & > a {
                    color: rgb(10, 171, 245);
                }
            }
        }
    }

    &.v {
        li {
            padding: 15px 25px;
        }
    }
}

#hamb-menu {
    position: relative;
    cursor: pointer;
    display: none;
    padding: 15px 10px;

    & > div {
        display: block;
        margin-top: 4px;
        width: 24px;
        border-bottom: 4px solid #fff;
        border-radius: 1px;
        transition: all 0.2s linear;
        transform-origin: 1px;
        
        &:first-of-type {
            margin: 0;
        }
    }

    &.hmb_active {
        :first-child {
            transform: rotate(45deg);
        }
      
        :nth-child(2) {
            opacity: 0;
            transform: translateX(20px);
        }
    
        :nth-child(3) {
            transform: rotate(-45deg);
        }
    }
}