#popups {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    #social-media-popup-container {
        position: fixed;
        color: #fff;
        right: 0;
        bottom: 25px;
        display: grid;
        grid-template-columns: auto auto;
        transform: translateX(50%);
        transition: 0.4s;
        z-index: 1000;

        &:hover {
            transform: translateX(0%);

            .open-popup {
                & > p {
                    & > * {
                        transform: rotate(-180deg);
                    }
                }
            }
        }

        .open-popup {
            width: 55px;
            height: 60px;
            border-top-left-radius: 50%;
            border-bottom-left-radius: 50%;
            background-color: black;
            display: flex;
            justify-content: center;
            align-items: center;
            
            & > p {
                position: relative;
                margin: 0;
                font-size: 24px;
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;

                & > * {
                    margin-left: 7px;
                    transform-origin: 50% 50%;
                    transition: all 0.5s;
                }
            }
        }

        .popup-social-list {
            background-color: black;
            border-bottom-left-radius: 10px;

            a {
                color: #fff;
                text-decoration: none;
            }
        }
    }

    .modal-container {
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: #fff;
        z-index: 10001;
        width: 650px;
        border-radius: 10px;

        .close-modal {
            padding: 10px 15px 10px 20px;
            font-size: 24px;
            display: grid;
            grid-template-columns: 1fr auto;

            & > * {
                cursor: pointer;
            }
        }

        .share-website-container {
            padding: 15px 20px;
            font-size: 20px;

            & > div {
                overflow: hidden;

                .social-medias {
                    display: grid;
                    grid-auto-flow: column;
                    align-items: center;
                    gap: 10px;
                    //overflow-x: scroll;
                }
    
                .copy-to-clipboard {
                    margin-top: 20px;
                    width: 100%;
                    cursor: pointer;
                    display: grid;
                    gap: 10px;
                    align-items: center;
                    grid-template-columns: 1fr auto;
                    padding: 15px 20px;
                    border: 1px solid #000;
                    color: #000;
                    border-radius: 5px;
    
                    &:active {
                        color: rgb(92, 92, 92);
                    }
                }
            }
        }
    }

    .display-block {
        display: block;
    }
    
    .display-none {
        display: none;
    }
}