main {
    margin: 25px 0 50px 0;

    #menu-container {
        .menu-display-type {
            display: grid;
            grid-template-columns: min-content min-content;
            gap: 10px;
            font-size: 24px;

            & > * {
                cursor: pointer;

                &:first-of-type {
                    transform: rotate(90deg);
                }
            }
        }

        .tab-view-container {
            position: relative;
            
            .tab-selectors-container {
                position: sticky;
                top: 80px;
                left: 0;
                display: grid;
                grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
                grid-template-rows: auto;
                border-bottom: 2px solid #000;
                z-index: 1;
                background: #fff;

                button {
                    padding: 10px 20px;
                    border: 0;
                    cursor: pointer;
                    background: transparent;
    
                    &.active {
                        background: #ffA500;
                    }
                }
            }

            #tabs-content-container {
                position: relative;
                background: #ffA500;
                overflow: hidden;
                min-height: 350px;
                transition: 1.5s;
                
                .tab-content {
                    position: absolute;
                    padding: 20px;
                    transform: translateX(100%);
                    width: 100%;
                    transition: 0.7s;
                    opacity: 0;
                    visibility: hidden;
    
                    &.active {
                        transform: translateX(0px);
                        opacity: 1;
                        visibility: visible;
                    }
                }
            }
        }
    }

    #contact-container {
        #google-map-container {
            width: 100%; 
            height: 500px;
            margin-top: 50px;
            margin-bottom: -50px;
    
            .mapouter {
                position: relative;
                text-align: right;
                height: 100%;
                width: 100%;
        
                .gmap_canvas {
                    overflow: hidden;
                    background: none !important;
                    height: 100%;
                    width: 100%;
        
                    iframe {
                        height: 100%;
                        width: 100%;
                    }
                }
            }
        }
    
        #address-panel {
            & > div {
                display: grid;
                grid-template-columns: auto 1fr;
                gap: 15px;
    
                & > .icon-container {
                    height: fit-content;
                    width: fit-content;
                    background-color: aqua;
                    padding: 15px;
                    border-radius: 50%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }
        }
    }
}