header {
    width: 100%;
    height: 100vh;
    position: relative;

    #topbar {
        position: fixed;
        width: 100%;
        background-color: #000;
        color: #fff;
        padding: 15px 0;
        z-index: 10000;

        #side-nav {
            position: fixed;
            right: 0;
            top: 0;
            height: 100vh;
            background: #000;
            padding-top: 38px;
            transform: translateX(100%);
            transition: 0.3s;

            &.active {
                transform: translateX(0);
            }
        }

        #language-selector {
            position: absolute;
            right: 0;
            bottom: 0;
            transform: translateY(50%);
            background-color: #000;
            padding: 10px;
            border-bottom-left-radius: 10px;

            $image-height: 1.7em;

            img {
                height: $image-height;
            }

            &:hover {

                & > .dropdown {
                    opacity: 1;
                    visibility: visible;

                    .flag-container {
                        opacity: 1;
                        visibility: visible;
                    }
                }
            }

            .dropdown {
                display: grid;
                grid-auto-rows: auto;
                gap: 20px;
                position: absolute;
                padding: 10px;
                padding-top: calc(#{$image-height} + 30px);
                top: 0;
                right: 0;
                height: 0;
                opacity: 0;
                visibility: hidden;
                background: #000;
                border-bottom-left-radius: 10px;
                transition: 0.3s;
                z-index: -1;

                .flag-container {
                    cursor: pointer;
                    opacity: 0;
                    visibility: hidden;
                    transition: 0.3s;
                    transition-delay: 0.3s;

                    img {
                        transition: 0.3s;
    
                        &:hover {
                            transform: scale(1.1);
                        }
                    }
                }
            }
        }
    }
    
    #topslider {
        position: relative;
        width: 100%;
        height: 100%;
        z-index: 1;
    
        & > .slideImageShade {
            position: absolute;
            height: 100%;
            width: 100%;
            top: 0;
            left: 0;
            background-color: rgba(0, 0, 0, 0.4);
            z-index: 2;
        }

        & > *:not(img) {
            z-index: 3;
        }

        .carousel, .carousel-inner, .carousel-item {
            height: 100%;
            width: 100%;
        }

        & > .carousel-container {
            position: relative;

            .logo {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                display: grid;
                grid-template-rows: auto auto auto;
                gap: 35px;    
                justify-items: center;
                width: 350px;

                & > * {
                    width: 100%;

                    &:last-of-type {
                        //width: 30%;
                    }
                }

                svg {
                    & > * {
                        fill: #fff;
                    }
                }
            }

            .carousel-control-prev, .carousel-control-next {
                width: 35px !important;
            }
    
            .carousel-indicators {
                margin-bottom: 100px;
    
                & > button {
                    width: 15px;
                    height: 15px;
                    margin-right: 7px;
                    margin-left: 7px;
                    border-radius: 50%;
                }
            }

            .carousel-caption {
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                margin: auto !important;
                height: fit-content;
                padding: unset;
                text-align: left;

                h3 {
                    margin: 0;
                }
            }
        }
    }
}